.About{height:100vh;max-height:100vh;overflow:hidden;display:flex}.About .content{flex:1 1;display:flex;flex-direction:column;justify-content:center;align-items:center}.About .content .text{max-width:37rem;margin:0 2rem}.About .content .text h1{text-transform:uppercase;color:#216bb5;font-size:clamp(4rem, 5vw, 4.5rem);font-weight:900}.About .content .text p{font-size:2rem;font-size:clamp(1.5rem, 3vw, 1.875rem);font-weight:300;line-height:130%}.About .image{flex:1 1;background:url(/ccac-2/static/media/background.9df38dbc.png);background-size:cover;background-position:bottom center}@media (max-width: 50rem){.About .image{display:none}}

.Hero{display:flex;align-items:center;justify-content:center;height:100vh;background:url(/ccac-2/static/media/background.a7aa47b7.png);background-size:cover}.Hero div{max-width:33rem;text-align:center;display:flex;flex-direction:column;align-items:center;justify-content:center;color:white}.Hero div>*{margin:0;margin-top:1rem}.Hero div h1{font-size:6rem;font-weight:800}.Hero div h2{font-size:1.85rem;font-weight:700}.Hero div p{max-width:80%;line-height:30px;font-family:1.125rem}.Hero div button{border-radius:5px;font-weight:900;font-size:1.85rem;color:white;text-transform:uppercase;padding:1rem 2rem;outline:none;border:3px solid white;background:none;cursor:pointer;transition:0.3s ease}.Hero div button:hover{background:white;color:#216bb5}

.Process{min-height:100vh;margin-bottom:2rem}.Process .header{background:#216bb5;width:100%}.Process .header h1{padding:2.7rem;font-size:4.5rem;text-align:center;text-transform:uppercase;font-weight:900;color:white;margin:0}.Process .steps{margin-top:10%;width:100%;display:flex;justify-content:center;flex-wrap:wrap}.Process button{display:block;margin:auto;margin-top:7%;border-radius:5px;font-weight:900;font-size:1.85rem;color:#216bb5;text-transform:uppercase;padding:1rem 2rem;outline:none;border:3px solid #216bb5;background:none;cursor:pointer;transition:0.3s ease}.Process button:hover{background:#216bb5;color:white}

.Step{display:inline-block;width:20%;margin:2rem;height:19rem}@media only screen and (max-width: 1520px){.Step{width:40%}.Step:nth-child(odd){display:flex;justify-content:flex-end}}@media only screen and (max-width: 750px){.Step{display:flex;justify-content:center !important;width:100%}}.Step .wrapper{display:flex;flex-direction:column;position:relative;width:19rem;border:5px solid #216bb5;border-radius:15px;height:100%}.Step .wrapper .number{width:3.5rem;height:3.5rem;position:absolute;top:0;left:0;-webkit-transform:translateX(-40%) translateY(-40%);transform:translateX(-40%) translateY(-40%);background:#216bb5;border:7px solid white;border-radius:50%;display:flex;justify-content:center;align-items:center}.Step .wrapper .number p{font-size:1.5rem;color:white;margin:0;font-weight:bold}.Step .wrapper h1{font-weight:1000;font-size:1.85rem;margin:2.5rem;height:5rem;margin-bottom:0;color:#216bb5}.Step .wrapper p{font-size:1.2rem;margin:0 2.5rem}

